import './Score.css'
import { Card, Badge } from '@material-ui/core'


const Score = (props) => {
    let content
    switch (content) {
        case !props.score:
            return <Badge badgeContent={props.score} color='warning'></Badge>
            break;
    
        default:
            break;
    }
    return(
        <Card className="score" style={{paddingBottom:12}}>
            <Badge badgeContent={content} color='primary'></Badge> 
            <br/>    
            <br/>            
            <Badge badgeContent={12} color='secondary'></Badge> 
        </Card>
    )
} 
export default Score